.burger_constructor__list {
  overflow-y: scroll;
  max-height: 464px;
}

.burger_constructor__list::-webkit-scrollbar {
  width: 8px;
}

.burger_constructor__list::-webkit-scrollbar-track {
  background-color: #2F2F37;
}


.burger_constructor__list::-webkit-scrollbar-thumb {
  background-color: #8585AD;
}

.burger_constructor__list::-moz-scrollbar-thumb {
  background-color: #8585AD;
}

/* Firefox */
/* Track */
.burger_constructor__list {
  scrollbar-width: 8px;
  scrollbar-color: #2F2F37 #8585AD;
}